/**
 * Gets the compare-price for a product
 * @param cmpPrice from product
 * @accepted_input_units l, cl, ml, kg, g
 * @returns price per kilogram or liter
 */
export function GetCmpPrice(cmpPrice: CompareData): number {
    let ret: number = 0;

    if (!cmpPrice.amount || !cmpPrice.unit) return 0;

    let convertPrice: { store: string; amount: string; } = { store: "", amount: cmpPrice.amount }
    let price: number = GetPrice([convertPrice]);

    // Get ml and g as defaults
    switch (cmpPrice.unit) {
        case "l":
        case "kg":
            ret = price * 1000;
            break;

        case "cl":
            ret = price * 10
            break;

        case "ml":
        case "g":
            ret = price;
            break;

        default:
            console.log(cmpPrice.unit + " can not be converted yet!");
            break;
    }

    return ret;
}
/**
 * Returns the lowest price of a product
 * @param stores a list of stores for a product
 * @returns lowest price 
 */
export function GetPrice(stores: { store: string; amount: string; }[]): number {
    let record = Infinity;

    for (let storePrice of stores) {
        let priceStr: RegExpMatchArray | null = storePrice.amount.match(/[0-9,]*/g);
        if (!priceStr) continue;

        let priceNum: number = Number(priceStr.join("").replace(",", "."));
        if (priceNum < record) { record = priceNum };
    }
    return record;
}

/**
 * Gets the size of the product
 * @param productSize amount of a unit eg "500g", "2l"
 * @accepted_input_units kg, g, l, ml
 * @returns size in grams
 */
export function GetSize(productSize: string): number {
    let ret: number = 0;

    let amountGrams: number;

    let unit: RegExpMatchArray | null | string = productSize.match(/[a-z]*/g);
    if (!unit) return 0;

    let match = productSize.match(/\d*/g);
    if (!match) return 0;

    unit = unit.join("")

    switch (unit) {
        case "l":
        case "kg":
            ret = Number(match[0]) * 1000;
            break;
        case "ml":
        case "g":
            ret = Number(match[0]);
            break;
        default:
            ret = 0;
            break;
    }

    return ret;
}