import { supported_sorting, supported_stores, supported_themes } from "@/assets/app.data";

export function GetLocalStorage(): LocalStorageDefinitions {
    // Loads the ret with default values
    let ret: LocalStorageDefinitions = {
        searchPreference: "cmp-price",
        colorTheme: "standard",
        storeMaxDistance: 5,
        prioritizedStores: [],
        promotionsPriority: true
    };

    let searchPref = localStorage.getItem("searchPreference") as unknown as AvailableSorting;
    let colorTheme = localStorage.getItem("colorTheme") as unknown as AvailableThemes;
    let storeMaxDistance = Number(localStorage.getItem("storeMaxDistance"));
    let prioritizedStores = localStorage.getItem("prioritizedStores");
    let promotionsPriority = localStorage.getItem("promotionsPriority");

    if (searchPref) {
        if (supported_sorting.includes(searchPref)) {
            ret.searchPreference = searchPref;
        }
    }

    if (colorTheme) {
        if (supported_themes.includes(colorTheme)) {
            ret.colorTheme = colorTheme;
        }
    }

    if (storeMaxDistance) {
        if (!isNaN(+storeMaxDistance)) {
            ret.storeMaxDistance = storeMaxDistance;
        }
    }

    if (prioritizedStores) {
        try {
            let prioStores: AvailableStores[] = JSON.parse(prioritizedStores);
            if (Array.isArray(prioStores)) {
                prioStores.forEach(store => {
                    if (supported_stores.includes(store)) {
                        ret.prioritizedStores.push(store);
                    }
                });
            }
        } catch (e) {
            // Just says that it does not work...
            console.error(e);
        }
    }

    if (promotionsPriority) {
        if (promotionsPriority == "true") {
            ret.promotionsPriority = true;
        } else {
            ret.promotionsPriority = false;
        }
    }

    return ret;
}

export function SetLocalStorageItem(key: string, value: string, asJson: boolean) {
    if (asJson) {
        value = JSON.stringify(value);
    }
    localStorage.setItem(key, value);
}