<template>
    <ShopSaverHeader>
        <input v-model="searchInput" placeholder="Search for a product" @keydown="Search($event)" :disabled="canSearch" />

        <select v-model="sorting" @change="SortCards">
            <option v-for="sortAlts in sortingAlts" :value="sortAlts.sort">{{ sortAlts.alt }}</option>
        </select>
    </ShopSaverHeader>

    <div v-if="priceConverter.formattedData.length > 0" class="result-container">
        <div v-for="product in priceConverter.formattedData" class="product-tile"
            @click="product.flipped = !product.flipped">
            <div v-if="!product.flipped">
                <ProductCardFront :product="product" />
            </div>
            <div v-else>
                <ProductCardBack :product="product" />
            </div>
        </div>
    </div>
</template>

<script lang="ts" setup>
// Components
import ProductCardFront from "@/components/ProductCardFront.vue";
import ProductCardBack from "@/components/ProductCardBack.vue";
import ShopSaverHeader from "@/components/ShopSaverHeader.vue"

// Functions and classes
import { getHemkop, getWillys, getIca } from "@/functions/search_functions.ts"
import { GetCmpPrice, GetPrice, GetSize } from "@/functions/product_functions.ts"
import { axfood } from "@/responses";
import adapter from "../adapter.ts";
import { ref, Ref } from "vue";

const priceConverter = ref(new adapter());

const searchInput = ref()
let canSearch = ref(false);
const sortingAlts = [{ sort: "high", alt: "Expensive" }, { sort: "low", alt: "Cheap" }, { sort: "cmp-price", alt: "Compare Price" }, { sort: "size", alt: "Size" }]
const sorting = ref();
sorting.value = sortingAlts[2].sort;

// This function sorts the cards
function SortCards() {
    switch (sorting.value) {
        case "high":
            priceConverter.value.formattedData = priceConverter.value.formattedData.sort((a, b) => GetPrice(b.data.prices) - GetPrice(a.data.prices))
            break;
        case "low":
            priceConverter.value.formattedData = priceConverter.value.formattedData.sort((a, b) => GetPrice(a.data.prices) - GetPrice(b.data.prices))
            break;
        case "cmp-price":
            priceConverter.value.formattedData = priceConverter.value.formattedData.sort((a, b) => GetCmpPrice(a.data.compare_price) - GetCmpPrice(b.data.compare_price))
            break;
        case "size":
            priceConverter.value.formattedData = priceConverter.value.formattedData.sort((a, b) => GetSize(b.data.size) - GetSize(a.data.size))
            break;
        default:
            console.log("Bad sorting! Sorting option does not exist!");
            break;
    }
}

// Adds start data - a search for pasta
setTimeout(() => {
    // @ts-ignore
    priceConverter.value.AddAxfood(axfood, "Hemköp");
    // @ts-ignore
    priceConverter.value.AddAxfood(axfood, "Willys");
}, 10) // Please don't note the random loading time 

// Handles the search
async function Search(e: KeyboardEvent) {
    if (e.key != "Enter") {
        return;
    }

    if (!searchInput.value) {
        console.log("Searchbar value undefined!")
        return;
    }

    // Reset the previous search value - mentioned problem in issue #21
    priceConverter.value.formattedData = [];

    canSearch.value = true;
    let res1 = getHemkop(searchInput.value).then((result) => priceConverter.value.AddAxfood(result, "Hemköp"));
    let res2 = getWillys(searchInput.value).then((result) => priceConverter.value.AddAxfood(result, "Willys"));

    Promise.all([res1, res2]).then(() => canSearch.value = false);
    // getIca();
}
</script> 

<style scoped>
input {
    float: left;
    padding: 10px;
    border-radius: 8px;
    border: none;
    margin-top: 5px;
    margin-left: 30vh;
    min-width: 70vh;
}

select {
    float: left;
    padding: 10px;
    border-radius: 8px;
    border: none;
    margin-top: 5px;
    margin-left: 50px;
    background-color: white;
}

.product-tile {
    width: 160px;
    margin: 20px;
    background-color: rgb(89, 230, 89);
    border-radius: 5%;
    box-shadow: 9px 16px 7px 8px rgba(0, 0, 0, 0.329);
    transition: scale 100ms ease-in-out;
}

.product-tile:hover {
    scale: 1.1;
}

.result-container {
    display: flex;
    flex-wrap: wrap;
}
</style>